@import url("https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500&display=swap");

* {
  box-sizing: border-box;
  font-family: "Inter", sans-serif;
}

body {
  margin: 0;
}

p {
  margin: 0;
}

input[type="range"] {
  outline: none;
  background: #d9d9d9;
  -webkit-appearance: none;
  height: 7px;
  width: 100%;
}

/* Below logic cannot be combined with commas. See for reference: https://css-tricks.com/sliding-nightmare-understanding-range-input/ */
input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 31px;
  width: 31px;
  background: #17c690;
  border-radius: 50%;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

input[type="range"]::-moz-range-thumb {
  -webkit-appearance: none;
  height: 31px;
  width: 31px;
  background: #17c690;
  border-radius: 50%;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border: 0;
}

input[type="range"]::-ms-thumb {
  -webkit-appearance: none;
  height: 31px;
  width: 31px;
  background: #17c690;
  border-radius: 50%;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

input[type="range"]::-webkit-slider-thumb:hover {
  cursor: pointer;
}

input[type="range"]::-webkit-slider-thumb:active {
  cursor: grabbing;
}

input[type="range"]::-moz-range-thumb:hover {
  cursor: pointer;
}

input[type="range"]::-moz-range-thumb:active {
  cursor: grabbing;
}

input[type="range"]::-ms-thumb:hover {
  cursor: pointer;
}

input[type="range"]::-ms-thumb:active {
  cursor: grabbing;
}

#bottom-container {
  display: none;
}

.global-container-width {
  max-width: 1400px;
  margin: 0 auto;
}

#hero-container {
  background: #17c690;
  padding: 150px 140px 250px 100px;
}

#hero-content-wrapper {
  display: flex;
  justify-content: space-between;
  position: relative;
}

#hero-text-container * {
  color: white;
}

#hero-text-container h1 {
  font-size: 64px;
  font-weight: 600;
  line-height: 40px;
  white-space: nowrap;
}

#hero-text-container p {
  max-width: 730px;
  font-size: 32px;
  font-weight: 300;
  line-height: 48px;
  margin-top: 74px;
}

@media (max-width: 1200px) {
  #hero-container * {
    white-space: normal !important;
  }

  #hero-container img {
    position: relative;
    top: 20px;
    right: 0;
  }

  #hero-container h1 {
    font-size: 50px;
    font-weight: 600;
    line-height: 55px;
  }

  #hero-container p {
    max-width: 530px;
    font-size: 22px;
    line-height: 33px;
  }
}

@media (max-width: 875px) {
  #hero-container {
    padding: 50px 26px 210px;
  }

  #hero-container img {
    position: absolute;
    top: 64px;
    right: 20px;
    width: 148px;
    height: 160px;
  }

  #hero-container h1 {
    font-size: 24px;
    font-weight: 600;
    line-height: 40px;
    white-space: nowrap !important;
    margin-bottom: 0;
  }

  #hero-container p {
    max-width: 210px;
    font-size: 14px;
    line-height: 21px;
    margin-top: 30px;
    margin-right: 130px;
  }
}

#categories-section {
  transform: translateY(-120px);
  border-radius: 132px;
  display: flex;
  flex-direction: column;
  padding: 104px 155px 0 120px;
  background: white;
  align-items: center;
}

.category-wrapper {
  display: flex;
  flex-direction: column;
  margin-top: 76px;
  max-width: 1400px;
  width: 100%;
}

.category-wrapper:first-child {
  margin-top: 0;
}

.category-title {
  letter-spacing: 1px;
  margin-bottom: 32px;
}

.category-bottom-container {
  display: flex;
  margin-top: 40px;
}

.slider-wrapper {
  position: relative;
  margin-left: 60px;
  flex-grow: 1;
  display: flex;
  align-items: center;
}

.slider-tooltip {
  position: absolute;
  top: -6px;
  left: 17px;
  transform: translate(-50%, -50%);
  width: 76px;
  height: 25px;
  border: 1px solid #d9d9d9;
  border-radius: 2px;
  text-align: center;
  font-style: normal;
  font-weight: 400;
  font-size: 13px;
  line-height: 150%;
  background: white;
  padding-top: 2px;
}

.slider-tooltip::before {
  position: absolute;
  bottom: -17px;
  left: 30px;
  content: "";
  width: 0;
  height: 0;
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
  border-right: 14px solid #d9d9d9;
  transform: rotate(270deg);
}

@media (max-width: 875px) {
  #categories-section {
    border-radius: 42px;
    padding: 80px 24px 0;
  }

  #categories-section img {
    max-width: 44px;
  }

  .slider-wrapper {
    margin-left: 28px;
  }

  .slider-tooltip {
    top: -19px;
  }
}

#count-btn {
  background: #17c690;
  border-radius: 50px;
  border: 0;
  padding: 22px 64px;
  font-weight: 700;
  font-size: 16px;
  color: white;
  cursor: pointer;
  width: 168px;
  margin-top: 90px;
  align-self: flex-end;
}

@media (max-width: 875px) {
  #count-btn {
    width: 92px;
    height: 35px;
    font-size: 12px;
    line-height: 18px;
    padding: 9px 28px;
    margin-top: 50px;
  }
}

#co2-summed-up-result-section {
  background: #424242;
  border-radius: 10px;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 110px 0;
}

#co2-summed-up-result-section p {
  font-weight: 600;
  font-size: 64px;
  color: white;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

#co2-result {
  opacity: 0;
  color: #17c690 !important;
  animation-name: fadeIn;
  animation-duration: 2500ms;
  animation-delay: 1000ms;
  animation-fill-mode: forwards;
  animation-timing-function: ease-out;
  margin-top: 12px;
}

@media (max-width: 1200px) {
  #co2-summed-up-result-section p {
    font-size: 50px;
  }
}

@media (max-width: 875px) {
  #co2-summed-up-result-section p {
    font-size: 24px;
    line-height: 40px;
  }

  #co2-summed-up-result-section {
    padding: 30px 24px;
    border-radius: 0;
  }
}

#carbon-footprint-section {
  background: #17c690;
  padding: 68px 114px 100px 120px;
  border-radius: 132px;
}

#carbon-footprint-section hr {
  width: 100px;
  height: 4px;
  background: white;
  border: 0;
  margin: 0;
}

#carbon-footprint-section p {
  margin-top: 40px;
  line-height: 24px;
  font-size: 16px;
  color: white;
  font-weight: 300;
}

#carbon-footprint-paragraphs {
  max-width: 780px;
}

#carbon-footprint-container {
  display: flex;
  justify-content: space-between;
  max-width: 1400px;
  margin: 0 auto;
}

#carbon-footprint-container img {
  align-self: flex-end;
}

@media (max-width: 1200px) {
  #carbon-footprint-container {
    flex-direction: column;
    justify-content: center;
  }

  #carbon-footprint-container img {
    max-width: 295px;
    align-self: center;
    margin-top: 50px;
  }
}

@media (max-width: 875px) {
  #carbon-footprint-section {
    border-radius: 42px;
    padding: 42px 24px;
  }

  #carbon-footprint-container p {
    font-size: 14px;
  }
}

#partners-logos-section {
  background: #17c690;
  width: 100vw;
}

#partners-logos-container {
  width: 100%;
  padding: 38px 114px 0 120px;
  background-color: white;
  border-bottom-left-radius: 132px;
  border-bottom-right-radius: 132px;
}

#partners-logos-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

#partners-logos-container img:first-child {
  max-width: 248px;
}

#partners-logos-container img:nth-child(2) {
  max-width: 377px;
  transform: translateY(15px);
}

#partners-logos-container img:nth-child(3) {
  max-width: 156px;
  transform: translateY(-40px);
  margin-right: 50px;
}

#partners-logos-container img:nth-child(4) {
  max-width: 270px;
  transform: translateY(10px);
}

@media (max-width: 1200px) {
  #partners-logos-container img:first-child {
    max-width: 148px;
  }

  #partners-logos-container img:nth-child(2) {
    max-width: 277px;
    transform: translateY(-3px);
  }

  #partners-logos-container img:nth-child(3) {
    max-width: 68px;
    transform: translateY(-14px);
    margin-right: 50px;
  }

  #partners-logos-container img:nth-child(4) {
    max-width: 170px;
    transform: translateY(0);
  }
}

@media (max-width: 875px) {
  #partners-logos-section {
    display: flex;
    justify-content: center;
    background: white;
    padding: 30px 24px;
  }

  #partners-logos-container {
    max-width: 350px;
    padding: 0;
  }

  #partners-logos-container img:first-child {
    max-width: 65px;
  }

  #partners-logos-container img:nth-child(2) {
    max-width: 99px;
    transform: translateY(3px);
  }

  #partners-logos-container img:nth-child(3) {
    max-width: 41px;
    transform: translateY(-11px);
    margin-right: 30px;
  }

  #partners-logos-container img:nth-child(4) {
    max-width: 71px;
    transform: translateY(2px);
  }
}

#bubble-chart-container {
  display: flex;
  justify-content: center;
}

#bubble-chart-section {
  padding: 80px 100px;
}

#bubble-chart-section p {
  line-height: 24px;
  font-size: 16px;
  font-weight: 300px;
}

@media (max-width: 875px) {
  #bubble-chart-section {
    padding: 16px 24px;
  }

  #bubble-chart-container {
    transform: scale(1.2);
  }
}
